@import url(https://fonts.googleapis.com/css2?family=Baloo+Tammudu+2:wght@500&display=swap);
.inputBox {
  display: grid;
  place-items: center;
  -webkit-flex: 1 1;
          flex: 1 1;
  height: 100hv;
}

.input_field {
  padding: 10%;
  text-align: center;
  background-color: white;
  box-shadow: 0px 20px 26px rgba(138, 138, 138, 0.3);
  padding-top: 0;
  padding-bottom: 50px;
  margin: 20px;
  border-radius: 20px;
}

.input_field > h1 {
  font-family: "Baloo Tammudu 2", cursive;
  height: 70px;
}
.input_field > p {
  font-family: "Baloo Tammudu 2", cursive;
  margin-top: 5px;
}

.input_field > img {
  padding-top: 5px;
  padding-bottom: 10px;
  width: 50%;
}

.input_field > form > button {
  margin-top: 30px;
  text-transform: inherit !important;
  /* background-color: #9a1eff !important; */
  color: white;
  background: rgb(82, 5, 181);
  background: linear-gradient(
    45deg,
    rgba(82, 5, 181, 1) 35%,
    rgba(255, 0, 121, 1) 92%,
    rgba(255, 0, 74, 1) 99%
  );
}

#Input {
  text-transform: inherit !important;
  color: #e703fc !important;
  font-family: "Baloo Tammudu 2", cursive;
}

#result {
  height: 100vh;
  place-items: center;
  display: grid;
}

.resutCard {
  padding-right: 90px !important;
  padding-left: 90px !important;
  text-align: center;
  background-color: white;
  box-shadow: 0px 20px 26px rgba(138, 138, 138, 0.3);
  padding-top: 10px;
  padding-bottom: 50px;
  border-radius: 20px;
}

.resutCard > button {
  text-transform: inherit !important;
  color: white;
  background: rgb(82, 5, 181);
  background: linear-gradient(
    45deg,
    rgba(82, 5, 181, 1) 35%,
    rgba(255, 0, 121, 1) 92%,
    rgba(255, 0, 74, 1) 99%
  );
}

