#result {
  height: 100vh;
  place-items: center;
  display: grid;
}

.resutCard {
  padding-right: 90px !important;
  padding-left: 90px !important;
  text-align: center;
  background-color: white;
  box-shadow: 0px 20px 26px rgba(138, 138, 138, 0.3);
  padding-top: 10px;
  padding-bottom: 50px;
  border-radius: 20px;
}

.resutCard > button {
  text-transform: inherit !important;
  color: white;
  background: rgb(82, 5, 181);
  background: linear-gradient(
    45deg,
    rgba(82, 5, 181, 1) 35%,
    rgba(255, 0, 121, 1) 92%,
    rgba(255, 0, 74, 1) 99%
  );
}
